// pages/login.tsx or app/login/page.tsx
import React, { useState } from "react";
import { useRouter } from "next/router";

interface LoginData {
  name: string;
  email: string;
  password: string;
  id: string;
  isAdmin: boolean;
}

const LoginPage: React.FC = () => {
  const [formData, setFormData] = useState<LoginData>({
    name: "",
    email: "",
    password: "",
    id: "",
    isAdmin: false,
  });
  const [error, setError] = useState<string>("");
  const router = useRouter();

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    if (!formData.email || !formData.password || !formData.id) {
      setError("Please fill all the fields");
      return;
    }

    
  };

  return (
    <>
      <div className="container mx-auto flex flex-col items-center h-screen justify-center">
      <h1 className="text-2xl text-center text-blue-500 m-4">Login Page</h1>
        <form onSubmit={handleSubmit} className=" border p-4 space--2 ">
            {error && <p className="text-red-500">{error}</p>}
          {/* <div>
            <label htmlFor="name" className="block">
              Name
            </label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              className="border p-2 "
              placeholder="Enter your name"
              required
            />
          </div> */}
          <div>
            <label htmlFor="email" className="block">
              Email
            </label>
            <input
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              className="border p-2"
              required
              placeholder="Enter your email"
            />
          </div>
          <div>
            <label htmlFor="password" className="block">
              Password
            </label>
            <input
              type="password"
              id="password"
              name="password"
              value={formData.password}
              onChange={handleChange}
              className="border p-2 "
              required
              placeholder="Enter your password"
            />
          </div>

          {/* <div>
            <label htmlFor="id" className="block">
              User ID
            </label>
            <input
              type="text"
              id="id"
              name="id"
              value={formData.id}
              onChange={handleChange}
              required
              className="border p-2 "
              placeholder="Enter your ID"
            />
          </div> */}

          <div>
            <label className="flex items-center">
              <input
                type="checkbox"
                name="isAdmin"
                required
                checked={formData.isAdmin}
                onChange={(e) =>
                  setFormData({ ...formData, isAdmin: e.target.checked })
                }
                className="mr-2"
              />
              Admin Login
            </label>
          </div>

          <div>
            <a href="/register">Create a User</a>
            <button type="submit" className="bg-blue-500 text-white p-2 ">
              Login
            </button>
          </div>
        </form>
      </div>
    </>
  );
};

export default LoginPage;
